<template>
  <div class="info" style="margin-top: 5%">
    <transition
      :duration="{ enter: 2500, leave: 1500 }"
      enter-active-class="animated fadeInRight">
      <div class="cont" v-if="show">
        <h1 style="margin-top: 35px; text-align: center; font-size: 25px; color: red"></h1>
        <template v-for="item in profiles">
          <el-col :span="12" :xs="24" :key="item.name">
            <el-card class="item" :body-style="{ padding: '0px'}">
              <img src="../../static/img/taomee.jpg" height="150" width="200" alt="">
              <div style="padding: 14px;">
                <span>{{item.name}}</span>
                <div class="bottom clearfix">
                  <time class="time">{{ currentDate }}</time>
                  <el-tooltip placement="top">
                    <div style="width: 200px" slot="content">{{item.content}}</div>
                    <el-button type='text'><i class="el-icon-info"></i></el-button>
                  </el-tooltip>
                </div>
              </div>
            </el-card>
          </el-col>
        </template>
      </div>
    </transition>
  </div>
</template>
<script>
export default {
  data () {
    return {
      show: false,
      currentDate: new Date(),
      profiles: [
        {
          name: '(learn)',
          skills: 'vue vue-cli vue-router vuex axios qiniu echart element-ui mavon-editor',
          content: '',
          github: 'https://github.com/lyttonlee/learn'
        },
        {
          name: '(express-server)',
          skills: 'node express mongoose mongodb3.4 express-promise-router async/await',
          content: 'express,mongodb3.4,async/awaitexpress-promise-routererrorRoBo 3T,postman!',
          github: 'https://github.com/lyttonlee/express-server-for-learn'
        },
        {
          name: 'MarkDown',
          skills: 'electron vue vue-cli vue-router axios qiniu nedb element-ui mavon-editor',
          content: ',nedbelectron!',
          github: 'https://github.com/lyttonlee/md-editor'
        },
        {
          name: 'if',
          skills: 'electron vue vue-cli element-ui',
          content: 'Demoif!',
          github: 'https://github.com/lyttonlee/if'
        }
      ]
    }
  },
  methods: {
    timeout () {
      setTimeout(() => {
        this.show = true
      }, 1000)
    }
  },
  mounted () {
    this.timeout()
  }
}
</script>

<style lang="less" scoped>
@import '../less/index.less';
.info {
  .info;
  .cont {
    .cont;
    width: 70%;
    .item {
      background-color: rgba(59, 123, 13, 0.2);
      padding: 10px;
      border-radius: 15px;
      margin: 10px auto;
      @media screen and (max-width: 768px) {
        padding: 5px;
        margin: 5px auto;
      }
      &:hover {
        border-radius: 25px;
        background-color: rgba(10, 135, 87, 0.3);
      }
      p {
        text-align: left;
        text-indent: 28px;
        font-size: 1.1rem;
        line-height: 1.2;
        @media screen and (max-width: 768px) {
          text-indent: 12px;
          font-size: 0.7rem;
          line-height: 1.1;
        }
      }
    }
  }
  .el-card {
    background: transparent;
    width: 90%;
  }
  .time {
    font-size: 13px;
    color: #999;
  }
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }
  .button {
    padding: 0;
    float: right;
  }
  .image {
    width: 100%;
    height: 50%;
    display: block;
  }
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
}
</style>



